.moviesContainer {
    background: #408E2F;
    width: 100%;
    height: 100vh;
  }
  
  .movie-view {
    margin-top: 50px;
    color: white;
  }
  
  .movie-poster {
    text-align: center;
  }
  .title, .description, .genre, .director, .actors, .year  {
    color: #1C2D19;
    font-size: 20px;
    font-weight: 400;
  }
  
  .movie-button-div{
    text-align: center;
    margin: 0 auto;
    width: 200px;
  }
  .movie-button {
    margin-top: 20px;
    width: 100%;
    background-color: #1C2D19;
  }